import popupLayer from '@/components/popup-layer.vue'
import Toast from '@/wxcomponents/dist/toast/toast';
export default {
	data() {
		return {
			// 控制底部弹出框
			shows: false,
			fabu: true,
			result: [],
			results: [],
			classId: [],
			wordId: [],
			classArr: [],
			selectedClassList: [],
			lookSelectedList: [],
			selectedClassNum: '',
			taskDate:''
		}
	},
	components: {
		popupLayer
	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1,
			})
		},
		// 选择单词
		onChanges(event) {
			this.result = event.detail
			console.log(this.result)
		},
		// 选择班级
		onChangess(event) {
			console.log(event)
			this.results = event.detail
			console.log(this.results)
			this.classId = []
			this.results.forEach(data => {
				this.classId.push(parseInt(data))
			})
			console.log(this.classId)
		},
		// 确认选择班级
		confirmClass() {
			this.selectedClassList = []
			this.results.forEach(data => {
				this.classArr.forEach(val => {
					if (data == val.classId) {
						this.selectedClassList.push(val.className)
					}
				})
			})

			console.log(this.selectedClassList)
			// this.selectedClassList = this.results
			this.selectedClassNum = this.results.length
			this.onClose()
		},
		// 控制---底部弹出框
		onClose() {
			this.shows = false
			this.fabu = true
		},
		showBox() {
			this.shows = true
			this.fabu = false
		},
		// 获取班级下拉列表
		classList() {
			this.$minApi.getClassList({
				schoolId: 1
			}).then(data => {
				this.classArr = data.data
				console.log(data)
			})
		},
		// 教师发布任务
		publish() {
			if (this.classId.length == 0) {
				Toast("请选择班级！")
				return false
			}

			console.log(this.classId)
			this.$minApi.publish({
				classId: this.classId.toString(),
				taskDate: this.taskDate,
				wordId: this.wordId.toString()
			}).then(data => {
				console.log(data)
				if (data.code == 200) {
					uni.switchTab({
						url: "../index/index"
					})
					Toast("任务布置成功！")
				} else {
					Toast(data.msg)
				}
			})
		}
	},
	created() {
		this.classList()
		uni.getStorage({
			key: 'selectedWords',
			success: (data) => {
				// console.log(data)
				this.lookSelectedList = data.data
				console.log(this.lookSelectedList)
				data.data.forEach(val => {
					this.result.push(val.wordId.toString())
					this.wordId.push(val.wordId)
				})
				console.log(this.result)
			}
		});
		uni.getStorage({
			key: 'taskDate',
			success: (data) => {
				this.taskDate=data.data
				console.log(data)
			}
		});
	},
	onLoad() {

	}
}
